:root {
  --border-radius: 16px;
  --modal-bg: #394b58;
  --border: 2px solid #394b58;
}

.modal-dialog {
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
}

.h4 {
  font-size: 1rem;
}

.modal .modal-dialog {
  max-width: 1100px !important;
  max-height: 90vh !important;
}

.modal-body {
  padding-top: 0;
  padding-bottom: 0;
  background-color: #fff;
  max-height: calc(100vh - 180px) !important;
  overflow-y: auto;
  overflow-x: hidden;
}

.modal-backdrop {
  opacity: 0 !important;
}

.modal-content {
  border-radius: var(--border-radius);
  border: var(--border);
  box-shadow: 0 0 10px 3px rgba(57, 75, 88, 0.3);
  background-color: #3b4a57;
}

.modal-header {
  background-color: #3b4a57;
  color: #fff;
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
}

.modal-footer {
  background-color: #fff;
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
}

.modal-footer .btn {
  border-radius: 10px;
}

.modal-footer .btn:hover {
  background-color: #3b4a57;
  transition: all 0.3 ease-in-out;
}

.modal-footer .btn:focus {
  outline: none !important;
  -webkit-appearance: none !important;

  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important;
}

.modal-text-header {
  font-weight: 500;
  padding-top: 20px;
}

.modal-text-availability-logo {
  height: 35px;
  display: block;
  margin: auto;
}

.modal-text-availability-logo > svg {
  height: 35px;
}

.modal-text-availability {
  height: 45px;
  display: flex;
  align-items: center;
}

.modal-content .nav-tabs {
  border: 0;
}

.modal-content .nav-link {
  color: #3b4a57;
  font-weight: 500;
  font-size: 15px;
}

.modal-table {
  max-width: 550px !important;
}

.dimensions_table_header {
  font-weight: 600;
  max-width: 60px;
}

.routes-table {
  max-height: 400px !important;
  overflow: auto;
}

.routes-departure {
  vertical-align: middle;
}

.modal-tech-table td {
  padding: 2px;
  border: 0;
}

.modal-tech-table td:hover {
  background-color: #fff;
  color: #000;
}

.mandatory_col,
.mandatory_title {
  max-width: 40px;
  text-align: center;
}

.example_col,
.example_title {
  max-width: 200px;
}
